import { FC, ReactNode } from 'react'
import clsx, { ClassValue } from 'clsx'
import { Link, LinkGetProps, useLocation } from '@reach/router'
import useLocalStorage from '@primer/hooks/useLocalStorage'
import { RouteConfig, isLinkRouteConfig, isAnchorRouteConfig, isNotNone } from '@primer/types'
import { ExternalLinkIcon } from '@heroicons/react/outline'
import Tooltip from '@primer/components/Tooltip'

type IntentValue = 'primary' | 'secondary' | 'white'

const intents = {
	primary: {
		wrapper: 'bg-gray-800 dark:bg-gray-900 antialiased',
		link: {
			active: 'text-white bg-gradient-to-r from-primary-600 to-primary-500 text-white dark:bg-gray-700 focus:bg-gray-900 dark:focus:bg-gray-800',
			inactive:
				'text-gray-200 hover:text-white hover:bg-gray-700 focus:text-white focus:bg-gray-700 dark:hover:bg-gray-800',
		},
	},
	secondary: {
		wrapper: 'bg-white dark:bg-gray-700 antialiased shadow',
		link: {
			active: 'text-gray-900 dark:text-gray-100 bg-gray-50 dark:bg-gray-800 focus:bg-gray-200 dark:focus:bg-gray-800',
			inactive:
				'text-gray-600 dark:text-gray-200 hover:text-gray-900 dark:hover:text-gray-200 dark:hover:bg-gray-900 hover:bg-gray-100 focus:text-gray-900 focus:bg-gray-100',
		},
	},
	white: {
		wrapper: 'bg-white antialiased shadow',
		link: {
			active: 'text-gray-900 bg-gray-50 focus:bg-gray-200',
			inactive: 'text-gray-600 hover:text-gray-900 hover:bg-gray-100 focus:text-gray-900 focus:bg-gray-100',
		},
	},
}

const getProps =
	(route: RouteConfig, min: boolean, intent: IntentValue) =>
	({ isCurrent, isPartiallyCurrent }: LinkGetProps) => {
		return (route.exact ? isCurrent : isPartiallyCurrent)
			? {
					className: clsx(
						'group flex items-center py-2 px-2.5 text-sm font-medium leading-5 rounded focus:outline-none transition ease-in-out duration-150',
						min && 'justify-center',
						intents[intent].link.active
					),
			  }
			: {
					className: clsx(
						'group flex items-center py-2 px-2.5 text-sm font-medium leading-5 rounded focus:outline-none transition ease-in-out duration-150',
						min && 'justify-center',
						intents[intent].link.inactive
					),
			  }
	}

const MenuLink = ({
	route,
	min,
	intent,
	children,
}: {
	route: RouteConfig
	min: boolean
	intent: IntentValue
	children: ReactNode
}) => {
	return isAnchorRouteConfig(route) ? (
		<a
			href={route.url}
			className={clsx(
				'group flex items-center justify-center py-2 px-2.5 text-sm leading-5 rounded focus:outline-none transition ease-in-out duration-150',
				intents[intent].link.inactive,
				min && 'justify-center'
			)}
			target="_blank"
			rel="noopener noreferrer"
		>
			<div className="flex items-center justify-center">{children}</div>
			{!min && (
				<div className="flex-1 flex justify-end">
					<ExternalLinkIcon className="opacity-25 w-4 h-4" />
				</div>
			)}
		</a>
	) : isLinkRouteConfig(route) ? (
		<Link to={`${process.env.PUBLIC_URL}${route.to}`} getProps={getProps(route, min, intent)}>
			{children}
		</Link>
	) : null
}

interface SidebarProps {
	header?: (min: boolean) => ReactNode
	footer?: (min: boolean) => ReactNode
	links: RouteConfig[]
	intent: IntentValue
	name: string
	className?: ClassValue
}

const Sidebar: FC<SidebarProps> = ({ header, footer, links, intent, name, className }) => {
	const [min, setMin] = useLocalStorage(`sidebar-${name}-min`, false)

	return (
		<div
			className={clsx(
				'flex flex-col h-screen relative',
				min ? 'w-16' : 'w-64',
				intents[intent].wrapper,
				className
			)}
		>
			<div
				className={clsx('absolute right-0 top-0 h-full -mr-2 w-4 z-40 group', {
					'cursor-right': min,
					'cursor-left': !min,
				})}
				onClick={() => setMin((x: boolean) => !x)}
			>
				<div className="group-hover:opacity-100 opacity-0 transition-opacity ease-in-out duration-200 w-1 h-full ml-1.5 bg-primary-600" />
			</div>

			<div className="flex-1 flex flex-col pt-5 pb-4 overflow-y-auto overflow-x-visible">
				{header && header(min)}

				<nav className="mt-5 flex-1 px-3 space-y-1">
					{links.filter(isNotNone).map((link, i) => (
						<NavLink key={i} link={link} intent={intent} min={min} />
					))}
				</nav>
			</div>

			{footer && footer(min)}
		</div>
	)
}

interface NavLinkProps {
	link: RouteConfig
	min: boolean
	intent: IntentValue
}

const NavLink: FC<NavLinkProps> = ({ link, min, intent }) => {
	const loc = useLocation()

	const matches = isLinkRouteConfig(link) ? loc.pathname.indexOf(link.to) === 0 : false

	return (
		<div>
			{link && (
				<Tooltip message={link.tooltip || link.title} enabled={min}>
					<MenuLink route={link} min={min} intent={intent}>
						<div className={clsx('opacity-75 pointer-events-none', min ? 'mr-0' : 'mr-3')}>{link.icon}</div>
						{!min && link.title}
					</MenuLink>
				</Tooltip>
			)}
			{matches && link.children && link.children.length > 0 && (
				<div className="mt-1">
					<div className="p-1 rounded-md space-y-1 border-2 border-gray-700">
						{link.children
							.filter((x) => x)
							.map((link, i) => (
								<NavLink key={i} link={link} intent={intent} min={min} />
							))}
					</div>
				</div>
			)}
		</div>
	)
}

export default Sidebar
