import { FC, useMemo } from 'react'
import { PageRendererProvider } from './utils'
import { planResource } from 'db/types'
import { Widget } from './utils'
import Stack from '@primer/components/Stack'
import { ViewComp } from './Widgets'

const PageRenderer: FC<{ id: string; unitId: string }> = ({ id, unitId }) => {
	const plan = useMemo(() => planResource(id), [id])

	const itemList: { items: Widget[] } = useMemo(() => {
		try {
			return JSON.parse(plan?.structure || 'null')
		} catch (error) {
			return null
		}
	}, [plan])

	return (
		<PageRendererProvider value={{ unitId, itemList }}>
			<Stack>
				{itemList.items.map((_, i) => (
					<ViewComp path={['items', i]} />
				))}
			</Stack>
		</PageRendererProvider>
	)
}

export default PageRenderer
