import { ChangeEvent, ElementType, FC, ReactNode, useState } from 'react'
import { useField, useFormikContext } from 'formik'
import clsx, { ClassValue } from 'clsx'
import useAutoFocus from '@primer/hooks/useAutoFocus'
import { FormValidations, validate } from './utils'
import { v4 as uuidv4 } from 'uuid'
import { ExclamationCircleIcon } from '@heroicons/react/solid'

type FormInputIntent = 'default' | 'overlap' | 'inset'

interface FormInputProps {
	as?: ElementType | string
	name: string
	label?: ReactNode
	type?: string
	intent?: FormInputIntent
	className?: ClassValue
	autoFocus?: boolean
	hideOptional?: boolean
	validations?: FormValidations
	placeholder?: string
	disabled?: boolean
	step?: number
	description?: string
	inputProps?: {
		min?: number
		max?: number
		step?: number
		className?: string
	}
}

const FormInput: FC<FormInputProps> = ({
	as = 'input',
	name,
	label,
	type = 'text',
	intent = 'default',
	className,
	autoFocus = false,
	hideOptional = false,
	validations = {},
	placeholder,
	disabled = false,
	description,
	inputProps = {},
}) => {
	const isRequired = validations?.required

	const ref = useAutoFocus<HTMLInputElement>(autoFocus)

	const Comp = as

	const [{ value }, { error, touched }, { setValue, setTouched }] = useField({
		name,
		type,
		validate: validate(validations),
	})

	const formik = useFormikContext()

	const [token] = useState(uuidv4())

	const { className: extraClassName, ...extraProps } = inputProps

	return intent === 'default' ? (
		<div className={clsx(className)}>
			{label && (
				<div
					className="block text-sm font-medium leading-5 text-gray-700 dark:text-gray-200 cursor-default"
					onClick={() => ref.current?.focus()}
				>
					{label}{' '}
					{!isRequired && !hideOptional && (
						<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
							(optional)
						</span>
					)}
				</div>
			)}
			<div className="text-[0px] leading-none select-none">{token}</div>

			<div className={clsx('relative rounded-md', label && 'mt-1')}>
				<Comp
					ref={ref}
					type={type}
					value={value}
					onChange={(e: ChangeEvent<HTMLInputElement>) => setValue(e.target.value)}
					onBlur={() => {
						setTouched(true)
					}}
					placeholder={placeholder}
					disabled={disabled || formik.isSubmitting}
					className={clsx(
						'form-input block w-full sm:text-sm sm:leading-5',
						extraClassName,
						error &&
							touched &&
							'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
					)}
					{...(Comp === 'input' ? extraProps : {})}
				/>
				{error && touched && (
					<div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
						<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
					</div>
				)}
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : intent === 'overlap' ? (
		<div className={clsx(className)}>
			<div
				className={clsx(
					'relative border rounded-md px-3 py-2 focus-within:ring-1 transition-all duration-200',
					error && touched
						? 'focus-within:ring-red-500 focus-within:border-red-500 border-red-300'
						: 'focus-within:ring-primary-500 focus-within:border-primary-500 border-gray-300'
				)}
			>
				{label && (
					<div
						className="absolute -top-2 left-2 -mt-px inline-block px-1 bg-white text-xs font-medium text-gray-900"
						onClick={() => ref.current?.focus()}
					>
						{label}{' '}
						{!isRequired && !hideOptional && (
							<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
								(optional)
							</span>
						)}
					</div>
				)}
				<div className="text-[0px] leading-none select-none">{token}</div>

				<div className={clsx('relative rounded-md', label && 'mt-1')}>
					<Comp
						ref={ref}
						type={type}
						value={value}
						onChange={(e: ChangeEvent<HTMLInputElement>) => setValue(e.target.value)}
						onBlur={() => {
							setTouched(true)
						}}
						placeholder={placeholder}
						disabled={disabled || formik.isSubmitting}
						className={clsx(
							'block w-full border-0 p-0 text-gray-900 placeholder-gray-500 focus:ring-0 sm:text-sm',
							extraClassName,
							error &&
								touched &&
								'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
						)}
						{...(Comp === 'input' ? extraProps : {})}
					/>
					{error && touched && (
						<div className="absolute inset-y-0 right-0 pr-0 pb-1 flex items-center pointer-events-none">
							<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
						</div>
					)}
				</div>
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : intent === 'inset' ? (
		<div className={clsx(className)}>
			<div
				className={clsx(
					'border rounded-md px-3 py-2 focus-within:ring-1 transition-all duration-200',
					error && touched
						? 'focus-within:ring-red-500 focus-within:border-red-500 border-red-300'
						: 'focus-within:ring-primary-500 focus-within:border-primary-500 border-gray-300'
				)}
			>
				<div className="block text-xs font-medium text-gray-900" onClick={() => ref.current?.focus()}>
					{label}{' '}
					{!isRequired && !hideOptional && (
						<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
							(optional)
						</span>
					)}
				</div>
				<div className="relative">
					<Comp
						ref={ref}
						type={type}
						value={value}
						onChange={(e: ChangeEvent<HTMLInputElement>) => setValue(e.target.value)}
						onBlur={() => {
							setTouched(true)
						}}
						placeholder={placeholder}
						disabled={disabled || formik.isSubmitting}
						className={clsx(
							'block w-full border-0 p-0 text-gray-900 placeholder-gray-500 focus:ring-0 sm:text-sm',
							extraClassName,
							error &&
								touched &&
								'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
						)}
						{...(Comp === 'input' ? extraProps : {})}
					/>
					{error && touched && (
						<div className="absolute inset-y-0 right-0 pr-0 pb-1 flex items-center pointer-events-none">
							<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
						</div>
					)}
				</div>
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : null
}

export default FormInput
