import { FC } from 'react'
import { useLocation, navigate, RouteComponentProps } from '@reach/router'
import { MailIcon, AcademicCapIcon, CubeIcon, PuzzleIcon, DocumentTextIcon, CogIcon } from '@heroicons/react/outline'
import { WorkflowIcon } from 'components/Icon'

export const links = [
	{
		title: 'Entities',
		to: '/settings/entities',
		icon: <CubeIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Roles',
		to: `/settings/roles`,
		icon: <AcademicCapIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Templates',
		to: `/settings/templates`,
		icon: <DocumentTextIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Notifications',
		to: '/settings/notifications',
		icon: <MailIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Workflows',
		to: '/settings/workflows',
		icon: <WorkflowIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Modules',
		to: '/settings/modules',
		icon: <PuzzleIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
	{
		title: 'Site Settings',
		to: '/settings/site-settings',
		icon: <CogIcon className="w-5 h-5" />,
		show: () => true,
		exact: true,
	},
]

const Admin: FC<RouteComponentProps> = ({ children }) => {
	const location = useLocation()

	if (location.pathname === '/settings') {
		navigate(links[0].to)
		return null
	}

	return <>{children}</>
}

export default Admin
