import { FC, ReactNode } from 'react'
import bg from 'assets/img/bg-grid.svg'
import clsx, { ClassValue } from 'clsx'

interface CanvasProps {
	className?: ClassValue
	noBackground?: boolean
	children: ReactNode
}

const Canvas: FC<CanvasProps> = ({ className, noBackground, children }) => {
	return (
		<div
			className={clsx('h-screen bg-gray-200 dark:bg-gray-800', className)}
			style={!noBackground ? { backgroundImage: `url(${bg})` } : {}}
		>
			{children}
		</div>
	)
}

export default Canvas
