import { FC, Fragment, useState } from 'react'
import { lensPath, over, set } from 'ramda'
import { navigate, RouteComponentProps } from '@reach/router'
import PageContent from '@primer/components/PageContent'
import Stack from '@primer/components/Stack'
import { PageBuilderProvider, Path } from './utils'
import DropTarget from './components/DropTarget'
import { uuid } from 'utils/funcs'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, CheckIcon, TemplateIcon } from '@heroicons/react/outline'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { unitResource } from 'db/types'
import db from 'db'
import Canvas from 'components/Layouts/Canvas'
import { Widget } from './utils'
import { tools, builds } from './Widgets'
import DraggableTool from './components/DraggableTool'

const PageBuilder: FC<RouteComponentProps<{ id: string; keyName: string }>> = ({ id, keyName }) => {
	const unit = unitResource(id || '-')
	const entity = unit?.entity
	const plan = entity?.plan(keyName || '-')

	const [name, setName] = useState(plan?.name || 'Page name')

	const [itemList, setItemList] = useState<{ items: Widget[] }>(
		JSON.parse(plan?.structure || 'null') || { items: [] }
	)

	const save = () => {
		if (!entity || !keyName) return

		let structure = JSON.stringify(itemList)

		if (plan?.data) {
			db.plans.save({
				...(plan.data || {}),
				name,
				structure,
			})
		} else {
			db.plans.save({
				id: uuid(),
				entityId: entity.id,
				key: keyName,
				name,
				structure,
			})
		}

		navigate(`${process.env.PUBLIC_URL}/${entity?.key}/${unit?.id}`)
	}

	const addItem = (path: Path, data: Widget) => {
		setItemList((l) => {
			let pathStart = path.slice(0, -1)
			let index = path.slice(-1)

			let newItemList = over(
				lensPath(pathStart),
				(arr) => {
					let before = arr.slice(0, index)
					let after = arr.slice(index)

					return [...before, data, ...after]
				},
				l
			)

			return newItemList
		})

		// interesting feature, might get annoying?
		// // @ts-ignore
		// editors[data.type]({ path, item: data, removeItem, updateItem })
	}

	const removeItem = (path: Path) => {
		setItemList((l) => {
			let pathStart = path.slice(0, -1)
			let index = path.slice(-1)

			let newItemList = over(
				lensPath(pathStart),
				(arr) => {
					let before = arr.slice(0, index)
					let after = arr.slice(+index + 1)

					return [...before, ...after]
				},
				l
			)

			return newItemList
		})
	}

	const updateItem = (path: Path, newValue: unknown) => {
		setItemList((l) => {
			let newItemList = set(lensPath(path), newValue, l)

			return newItemList
		})
	}

	return (
		<DndProvider backend={HTML5Backend}>
			<PageBuilderProvider value={{ entity, itemList, addItem, removeItem, updateItem }}>
				<Canvas className="flex flex-col">
					<div className="flex justify-between bg-white shadow h-16">
						<div className="flex items-center p-4 border-r border-gray-300">
							<Actions
								actions={{
									icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
									intent: 'borderless',
									rounded: 'full',
									onClick: () => window.history.back(),
								}}
							/>
						</div>
						<div className="flex-1 flex items-center px-6 border-r border-gray-300">
							<div className="text-2xl font-medium">Edit Page Plan</div>
						</div>
						<div className="flex items-center px-6">
							<Actions
								actions={[
									{
										title: 'Save',
										intent: 'save',
										icon: <CheckIcon className="w-5 h-5" />,
										onClick: save,
									},
								]}
							/>
						</div>
					</div>

					<div className="flex-1 flex">
						<div className="w-80 min-w-80 bg-white shadow border-t border-gray-200">
							<div className="p-4 size-lg font-medium border-b border-gray-200 flex items-center space-between space-x-4">
								<TemplateIcon className="w-5 h-5 text-gray-500" />
								<div className="flex-1">Page Elements</div>
							</div>
							<Stack className="p-4">
								<div>
									{Object.entries(tools).map(([type, tool]) => (
										<Fragment key={type}>
											<DraggableTool tool={tool} />
										</Fragment>
									))}
								</div>
								<div className="group flex items-center h-6 w-full">
									<div className="group-hover:ring-2 ring-primary-500 transition-all duration-100 h-1 group-hover:bg-primary-500 rounded-full w-full" />
								</div>
							</Stack>
						</div>

						<PageContent className="py-6">
							<div className="flex items-center space-x-1 mb-6 mt-2">
								<Actions
									actions={{
										icon: <ArrowLeftIcon className="w-5 h-5 -mx-2 text-primary-500" />,
										intent: 'borderless',
										rounded: 'full',
										onClick: () => {},
									}}
								/>
								<input
									type="text"
									value={name}
									onChange={(e) => setName(e.target.value)}
									className="flex-1 flex items-center text-2xl leading-8 font-semibold text-gray-900 dark:text-white sm:text-3xl sm:leading-9 whitespace-nowrap bg-transparent border-none focus:ring-2 focus:ring-primary-500 rounded-md"
								/>
							</div>

							<Stack space={2}>
								{itemList.items.map((item, i) => (
									<Fragment key={item.id}>
										<DropTarget path={['items', i]} isFirst={i === 0} accept={Object.keys(tools)} />
										{(() => {
											let BuildComp = builds[item.type]

											if (BuildComp) {
												return <BuildComp path={['items', i]} />
											}
											return null
										})()}
									</Fragment>
								))}
								<DropTarget
									path={['items', itemList.items.length]}
									isFirst={itemList.items.length === 0}
									isLast
									accept={Object.keys(tools)}
								/>
							</Stack>
						</PageContent>
					</div>
				</Canvas>
			</PageBuilderProvider>
		</DndProvider>
	)
}

export default PageBuilder
