import { FC, forwardRef, MouseEventHandler, ReactNode, RefObject } from 'react'
import clsx, { ClassValue } from 'clsx'

interface CardProps {
	className?: ClassValue
	header?: ReactNode
	footer?: ReactNode
	children?: ReactNode
	onClick?: MouseEventHandler<HTMLDivElement>
	onMouseOver?: MouseEventHandler<HTMLDivElement>
	onMouseOut?: MouseEventHandler<HTMLDivElement>
	wrapperRef?: RefObject<HTMLDivElement>
}

const Card: FC<CardProps> = ({ className, header, footer, children, onClick, onMouseOver, onMouseOut, wrapperRef }) => (
	<CardWrapper
		className={clsx(className)}
		onClick={onClick}
		onMouseOver={onMouseOver}
		onMouseOut={onMouseOut}
		ref={wrapperRef}
	>
		{header}
		{children && <CardContent>{children}</CardContent>}
		{footer && <CardFooter>{footer}</CardFooter>}
	</CardWrapper>
)

export const CardWrapper = forwardRef<HTMLDivElement, Omit<CardProps, 'header' | 'footer'>>(
	({ className, children, onClick, onMouseOver, onMouseOut }, ref) => (
		<div
			ref={ref}
			className={clsx('bg-white dark:bg-gray-700 shadow rounded-none sm:rounded-md', className)}
			onClick={onClick}
			onMouseOver={onMouseOver}
			onMouseOut={onMouseOut}
		>
			{children}
		</div>
	)
)

export const CardContent: FC<{ children: ReactNode; className?: ClassValue }> = ({ className, children }) => (
	<div className={clsx('px-4 py-5 sm:p-6', className)} data-card>
		{children}
	</div>
)

export const CardFooter: FC<{ children: ReactNode; className?: ClassValue }> = ({ className, children }) => (
	<div className={clsx('bg-gray-50 dark:bg-gray-750 sm:rounded-b-md px-4 py-4 sm:px-6', className)} data-card>
		{children}
	</div>
)

export default Card
