import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { EntityResource, entityResource } from '.'

export interface PlanModel extends Model {
	entityId: FK
	key: string
	name: string
	structure: string
}

export interface PlanResource extends PlanModel {
	readonly data: PlanModel | null
	readonly entity: EntityResource | null
}

export const planResource = (id: Identifier<PlanModel>): PlanResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.plans.find(id) : id

	if (!data) return null

	return {
		...data,
		data,
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
	}
}
